.add-article-section{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;grid-gap:1rem;gap:1rem}.add-article-section .form-container{display:-webkit-flex;display:flex}.add-article-section .form-container .form-container__label{font-weight:500}.add-article-section .form-container .form-container__select,.add-article-section .form-container .form-container__input,.add-article-section .form-container .form-container__textarea{padding:0.375rem 0.75rem;color:#495057;background-color:#fff;background-clip:padding-box;border:1px solid #ced4da;border-radius:0.25rem;transition:border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out}.add-article-section .form-container .form-container__select:focus,.add-article-section .form-container .form-container__input:focus,.add-article-section .form-container .form-container__textarea:focus{border-color:#f09851;outline:0;box-shadow:0 0 0 0.2rem rgba(233,133,51,0.35)}.add-article-section .form-container .form-container__small-inputs{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:0.5rem;gap:0.5rem}.add-article-section .form-container .form-container__small-inputs .form-container__select,.add-article-section .form-container .form-container__small-inputs .form-container__input{height:2rem}.add-article-section .form-container .form-container__small-inputs .form-container__input{height:1.375rem}.add-article-section .form-container .form-container__big-inputs{position:relative;left:2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:0.5rem;gap:0.5rem}.add-article-section .add-article-section__add-button{margin:1rem 0;padding:0.75rem 1.25rem;border:none;border-radius:6px;background-color:#e98533;font-size:large;color:#dce9e8;cursor:pointer}.add-article-section .add-article-section__add-button:hover{background-color:#eba055}.add-article-section .add-article-section__add-button:active{-webkit-transform:translate(0, 5%);transform:translate(0, 5%)}@media screen and (max-width: 700px){.add-article-section .form-container{-webkit-flex-direction:column;flex-direction:column}.add-article-section .form-container .form-container__big-inputs{position:static;margin-top:0.5rem}}

.header{height:100px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:left;justify-content:left;grid-gap:2rem;gap:2rem;-webkit-align-items:center;align-items:center;background-color:#efeeec}.header .header__image{height:100px;width:100px;margin:0 1.5rem}.header .header__link{font-size:large;font-weight:bold;text-decoration:none;color:black;-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-transform:translateZ(0);transform:translateZ(0);transition:-webkit-transform 0.2s;transition:transform 0.2s;transition:transform 0.2s, -webkit-transform 0.2s}.header .header__link:hover{-webkit-transform:scale(1.1) translate(0, 12%);transform:scale(1.1) translate(0, 12%)}.welcome-section{text-align:center}@media screen and (max-width: 426px){.header{grid-gap:1rem;gap:1rem}.header .header__image{margin:0}}

